.dashboard-cont {
  width: 360px;
  height: 800px;
  background: transparent linear-gradient(180deg, #000000 0%, #000e09 100%) 0%
    0% no-repeat padding-box;
  mix-blend-mode: normal;
  border-radius: 28px;
  opacity: 1;
}

.prof-logo {
  margin-top: 44px;
  margin-left: 280px;
  width: 40px;
  height: 40px;
  background: transparent 0% 0% no-repeat padding-box;
  mix-blend-mode: normal;
  opacity: 1;
}

.moptro-logo {
  top: 104px;
  left: 572px;
  width: 71px;
  height: 70px;
  background: transparent 0% 0% no-repeat padding-box;
  mix-blend-mode: normal;
  opacity: 1;
}

.home-num {
  margin-left: 200px;
  margin-bottom: 0px;
  width: 35px;
  height: 35px;
  border-radius: 50px;
  font: normal normal normal 18px/25px Source Sans Pro;
  color: #36a546;
  background: #5e5e5eb5 0% 0% no-repeat padding-box;
  box-shadow: inset 0px 4px 0px #00000040;
  mix-blend-mode: normal;
  border: 1px solid #fffefe00;
  opacity: 0.5;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.dashboard-card {
  margin-top: 20px;
  margin-left: 15px;
  width: 328px;
  height: 465px;
  background: #5e5e5e82 0% 0% no-repeat padding-box;
  box-shadow: inset 0px 4px 0px #00000040;
  mix-blend-mode: normal;
  opacity: 0.4;
  /* filter: blur(2px); */
  border-radius: 20px;
}
.epd {
  height: 25px;
  text-align: center;
  font: normal normal normal 18px/25px Source Sans Pro;
  letter-spacing: 0.01px;
  opacity: 0.84;
  color: #ffffff;
}
.epd-cont {
  width: 328px;
  height: 45px;
  border-radius: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: #787d7d99;
}
.days {
  top: 302px;
  left: 473px;
  width: 179px;
  height: 15px;
  text-align: left;
  font: normal normal normal 12px/16px Mulish;
  letter-spacing: 0.01px;
  color: #ffffff;
  opacity: 1;
  margin-bottom: 0px;
}
.percent {
  font: normal normal normal 14px/19px Mulish;
  letter-spacing: 0.01px;
  color: #81d68d;
  margin-bottom: 0px;
}
.days-per-cont {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-top: 20px;
  margin-bottom: 0px;
}
